/* Estilo para o corpo do site */
body {
  font-family: 'Merriweather', serif; /* Aplica a fonte Merriweather para todo o conteúdo */
  background-color: #f0e5e5; /* Cor de fundo */
  color: #333333; /* Cor do texto */
  margin: 0;
  padding: 0;
}

/* Estilo para o contêiner principal do site */
.app-container {
  max-width: 1200px;
  margin: 40px auto;
  padding: 20px;
  background-color: #622b2b;
  border-radius: 15px;
  box-shadow: 0px 4px 6px rgba(0, 0, 0, 0.1);
}

/* Estilo para o cabeçalho */
.header h1 {
  font-family: 'Merriweather', serif;
  font-weight: bold;
  color: rgb(0, 0, 0);
  text-align: center;
  background-color: rgba(255, 255, 255, 0.7);
  padding: 10px;
  border-radius: 8px;
}

/* Estilo para o parágrafo */
p {
  text-align: center;
  font-size: 1.2em;
  color: #ffffff;
  margin-top: 10px;
}

/* Estilos para o Alice Carousel */
.alice-carousel {
  position: relative;
  max-width: 100%;
  height: 800px; /* A altura do carrossel pode ser ajustada */
  margin-top: 20px;
}

/* Estilo para as imagens do carrossel */
.carousel-image {
  width: 100%; /* A imagem ocupa toda a largura disponível */
  height: 100%; /* A imagem vai preencher toda a altura do carrossel */
  object-fit: cover; /* Faz com que a imagem se ajuste sem distorcer */
  border-radius: 10px; /* Arredonda as bordas das imagens */
  box-shadow: 0px 4px 6px rgba(0, 0, 0, 0.3); /* Sombra para destacar as imagens */
}

/* Responsividade para telas pequenas */
@media (max-width: 768px) {
  .app-container {
    padding: 15px;
    margin: 20px auto;
  }

  .header h1 {
    font-size: 2em;
  }

  .carousel-image {
    max-height: 300px; /* Reduz a altura máxima das imagens */
    object-fit: cover; /* Garante que a imagem continue preenchendo a área */
  }
}

/* Responsividade para celulares */
@media (max-width: 480px) {
  .app-container {
    padding: 10px;
    margin: 10px auto;
  }

  .header h1 {
    font-size: 1.5em;
  }

  .carousel-image {
    max-height: 200px; /* Ajusta a imagem para telas menores */
  }

  p {
    font-size: 1em; /* Ajusta o tamanho do texto */
  }
}
